<template>
    <div class="format_reg_layout active"><!-- 장례식장 사이드메뉴-->
		<div class="address_search_inner">
			<h1>폴더추가</h1>
      <div class="filesubject" >
          <select v-model="form.formFormType.id" class="input01">
						<option value="">폴더 선택</option>
						<option v-for="(formatType, index) in getFormatTypeList" :key="index" :value="formatType.id" >{{formatType.formTypeTitle}}</option>
					</select>
          <div class="fileArea">

            <button class="btn_up" ref="fileuploadbtn" @click="fileUpload"><div class="btn_up_txt">{{files.length===0?'파일 업로드':files[0].name}}</div></button>
					  <input type="file" ref="fileupload" @change="changeFiles" multiple style="display:none;"/>
			    </div>
      </div>
			<div class="btn_service_area"><button  @click="submit" class="btn_confirm">추가</button></div>
			<div class="address" style="margin-top:10px;">
		</div>
		</div>
		<button class="btn_address_colse" @click="closePopup()">닫기</button>
	</div>
</template>


<script>
import myMixin from '@/mixin/mixin'
import { mapGetters } from 'vuex'
  export default {
    name:'FormatAdd',
	  mixins: [myMixin],
    components: {

    },
    props:['folderId','closePopup'],
    data(){
        return{	
          form:{
            id:null,
            formTitle:'',
            formExtension:'',
            formMember:null,
            formType:{
              codeId:'USE-0'
            },
            formFormType:{
              id:''
            }
          },
            files:[]
        }
    },
    updated(){
      
    },
      mounted(){
        if(this.folderId!==null && this.folderId !== undefined){
          this.form.formFormType.id = this.folderId
        }
        if(this.getFormatTypeList.length===0){
          this.$store.dispatch('REQUEST_FORMAT_FOLDER_LIST')
        }
    },
	computed:{	
		...mapGetters(['getFormatTypeList']),
	},
    methods: {
        fileUpload(){
            this.$refs.fileupload.click()
        },
        changeFiles(event){
          
            this.files = event.target.files
            this.form.formTitle = event.target.files[0].name
            this.form.formExtension = this.fileExtension(this.form.formTitle)
        },     
        submit(){
            const parameter={
                form: this.form,
                files: this.files,
                formatType:
                  {
                    id:this.folderId
                  }
            }
          this.$store.dispatch("REQUEST_REGIST_FORMAT", parameter) 
          this.reset()
        },
        reset(){
          this.form = {
            id:null,
            formTitle:'',
            formExtension:'',
            formMember:null,
            formType:{
              codeId:'USE-0'
            },
            formFormType:this.form.formFormType
          }
          this.files = []

          this.closePopup()
          
        }
    },
  };
</script>